.hidden {
  opacity: 0;
}

html {
  height: 100%;
}

body {
  height: 100%;
  min-width: 320px;
  overflow-x: hidden;
  font-family: "franklin-gothic-atf", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #121212;
}
@media (max-width: 480px) {
  body {
    font-size: 16px;
  }
}

.page {
  position: relative;
  padding-top: 32px;
  padding-bottom: 32px;
  min-height: 100%;
}

.webgraphic-page {
  background: url("../../images/page-bg.jpg") no-repeat;
  background-size: cover;
}

.container {
  position: relative;
  max-width: 1200px;
  margin: 0px auto;
  overflow: hidden;
}

.video {
  height: 100%;
  width: 100%;
  height: 100vh;
  width: 100vw;
}

.inner-container {
  position: relative;
  max-width: 900px;
  margin: 0px auto;
  overflow: hidden;
  box-shadow: 0px 0px 40px 20px rgba(18, 18, 18, 0.2);
}

.logo {
  height: 70px;
  margin-right: 32px;
}

h1 {
  margin-top: 80px;
  margin-bottom: 32px;
  font-size: 3rem;
  font-weight: 600;
}

h1.heading {
  margin-top: 32px;
  margin-bottom: 48px;
  text-transform: uppercase;
  font-size: 4rem;
  font-weight: 600;
  line-height: 1;
}
@media (max-width: 480px) {
  h1.heading {
    font-size: 3rem;
  }
}

.intro {
  font-weight: 600;
  max-width: 400px;
  margin-bottom: 32px;
}

ul,
li,
p {
  margin-bottom: 16px;
  line-height: 1.5;
  max-width: 60ch;
}

a {
  display: inline-block;
  font-weight: 600;
}
a:link, a:visited {
  color: #112553;
}

a.cta {
  background: rgba(0, 121, 52, 0.8);
  text-transform: uppercase;
  padding: 16px 32px;
  border-radius: 8px;
  backdrop-filter: blur(6px);
  font-size: 1.6rem;
  margin-bottom: 32px;
}
a.cta:link, a.cta:visited {
  color: #fff;
}
@media (max-width: 480px) {
  a.cta {
    font-size: 1rem;
    text-align: center;
  }
}

a.back {
  display: block;
  width: 80px;
  text-align: center;
  position: relative;
  margin: 32px auto;
  padding: 8px 16px;
  background: #fff;
  border-radius: 8px;
}

.scroll-down {
  display: block;
  width: 80px;
  text-align: center;
  position: relative;
  margin: 32px auto;
  z-index: 99;
}
.scroll-down img {
  margin-top: 8px;
  height: 22px;
}

img {
  max-width: 100%;
  height: auto;
}

.heading {
  margin-top: 32px;
  margin-bottom: 32px;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1;
}
@media (max-width: 480px) {
  .heading {
    font-size: 2rem;
  }
}

.small {
  margin-top: 8px;
  font-size: 1rem;
  text-transform: none;
}

.stat {
  font-size: 2rem;
  line-height: 1;
  margin: 8px 0;
  font-weight: 600;
  color: #5ba423;
  display: inline-block;
}

.mb-4 {
  margin-bottom: 64px;
}

.box {
  padding: 8px 19.2px;
  border-radius: 8px;
  max-width: 480px;
  position: relative;
  z-index: 99;
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(6px);
  display: flex;
  align-items: center;
}
@media (max-width: 480px) {
  .box {
    flex-direction: column;
    padding: 16px;
  }
}
.box span {
  display: block;
}
.box p:last-child {
  margin-bottom: 8px;
}
.box img {
  width: 50px;
  margin-right: 16px;
  display: inline-block;
}
@media (max-width: 480px) {
  .box img {
    margin-right: 0;
    margin-bottom: 16px;
  }
}

ul {
  list-style: disc outside;
  margin-left: 32px;
  margin-bottom: 64px;
}
ul.col {
  columns: 2;
}
@media (max-width: 480px) {
  ul.col {
    columns: 1;
  }
}

li {
  font-weight: 600;
}

.section {
  position: relative;
  padding-top: 48px;
  padding-bottom: 48px;
}
.section.light {
  color: #fff;
}

.content {
  position: relative;
  z-index: 99;
  margin: 0 32px;
}

.top-scene {
  background: rgb(22, 131, 206);
  background: linear-gradient(170deg, rgb(22, 131, 206) 0%, rgb(71, 195, 228) 50%);
}

.pane {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
}
@media (max-width: 901px) {
  .pane {
    display: none;
  }
}

.m {
  display: none;
}
@media (max-width: 901px) {
  .m {
    display: block;
    position: absolute;
  }
  .m.m_0 {
    bottom: 0;
  }
  .m.m_1 {
    top: -100px;
  }
}

.sky {
  min-height: 880px;
}
@media (max-width: 901px) {
  .sky {
    min-height: auto;
  }
}
.sky h1,
.sky .heading,
.sky .intro {
  color: #112553;
}
.sky .pane {
  width: 100%;
}
@media (max-width: 901px) {
  .sky .pane {
    display: block;
  }
}
.sky .p_0 {
  top: -100px;
}
.sky .p_0 .cloud-1 {
  width: 100%;
  background: url("../../images/cloud-1.png") no-repeat;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  animation: bg-slide 90s linear infinite;
  animation-delay: -55s;
  opacity: 0.9;
}
@media (max-width: 901px) {
  .sky .p_0 .cloud-1 {
    background-size: contain;
  }
}
.sky .p_1 {
  top: 340px;
}
.sky .p_1 .cloud-2 {
  width: 100%;
  background: url("../../images/cloud-2.png") no-repeat;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  animation: bg-slide 60s linear infinite;
  animation-delay: -10s;
  opacity: 0.9;
}
@media (max-width: 901px) {
  .sky .p_1 .cloud-2 {
    background-size: contain;
  }
}

@keyframes bg-slide {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(100%);
  }
}
.scene {
  min-height: 1100px;
}
@media (max-width: 901px) {
  .scene {
    min-height: auto;
    padding-bottom: 550px;
  }
}
.scene .p_0 {
  top: -5%;
}
.scene .p_1 {
  top: 0%;
}
.scene .p_2 {
  top: 30%;
}
.scene .box {
  margin-bottom: 150px;
}
.scene .box.b_0 {
  margin-left: auto;
  margin-top: -150px;
  margin-bottom: 100px;
}
.scene .box.b_2 {
  margin-left: auto;
  margin-bottom: 100px;
}
@media (max-width: 901px) {
  .scene .box.b_0, .scene .box.b_1, .scene .box.b_2, .scene .box.b_3 {
    top: auto;
    left: auto;
    right: auto;
    margin-top: auto;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 48px;
  }
}

.bottom {
  min-height: 1111px;
}
.bottom .p_0 {
  top: 0;
  z-index: 1;
}
.bottom .d_0 {
  animation-delay: 100ms;
}
.bottom .d_1 {
  animation-delay: 300ms;
}
.bottom .d_2 {
  animation-delay: 500ms;
}
.bottom .d_3 {
  margin-top: 32px;
  animation-delay: 1s;
}
.bottom .cta {
  animation-delay: 700ms;
}
.bottom .bug-light {
  position: absolute;
  z-index: 2;
  bottom: 261px;
  right: 203px;
}
.bottom .bug {
  position: absolute;
  z-index: 3;
  bottom: 390px;
  right: 324px;
}
.bottom .h_5 {
  margin-top: 100px;
}
.bottom .h_6 {
  margin-top: 200px;
}
@media (max-width: 901px) {
  .bottom .h_6 {
    margin-top: 48px;
  }
}
@media (max-width: 901px) {
  .bottom {
    background-color: #050c22;
    padding-bottom: 300px;
  }
}

